* {
  border-radius: 8px;
}
.hidden {
  display: none;
}

.editButton {
  position: absolute;
  left: 4px;
}
.routines {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

  height: 100vh;
  position: absolute;
  margin-top: 107px;
  padding: 8px;
  margin-right: 14px;
}
#myRoutines {
  display: flex;
  flex-direction: row;
  width: 100%;
  border-radius: 0;
  background-image: linear-gradient(skyblue, white);
}
.modal {
  display: flex;
  position: absolute;
  z-index: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
  color: white;
  text-decoration: underline;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-size: 35px;
  width: 100vw;
  height: 100vh;
  text-decoration: none;
}
.activities {
  width: 100%;
  max-height: 75%;
  padding-left: 5px;
  padding-right: 5px;
  margin-bottom: 60px;
  overflow-y: auto;
}

.routineDetails {
  display: flex;

  justify-items: center;
  flex-direction: column;

  border: none;
  width: 50vw;
  height: 50vh;
  background-color: white;
  color: black;
  box-shadow: 2px 2px 2px black;
}

.routineActivity {
  text-decoration: none;
  border: 1px solid black;
  width: 90%;
  padding: 5px;
}

.routineDetailsTitle {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;

  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  width: 100%;
  background: #062f4f;
  color: white;
  font-size: 24px;
  align-items: left;
  padding-left: 5px;
  padding-right: 2px;
}

.deleteRoutineButton {
  position: relative;
  left: 90%;
}

.title {
  position: absolute;
  left: 30px;
  white-space: nowrap;
}

.routineDetails {
  position: absolute;
  border-radius: 8px;
  background-color: #f6f6f6;
  font-size: 18px;
  display: flex;
  align-items: center;
  padding: 3px;
}

.routineBody {
  border-radius: 0;
}
.routineGoal {
  position: relative;
  text-align: left;
}
.routineActivity {
  font-size: 16px;
  text-decoration: none;
  margin-top: 4px;
  border: 1px solid lightgray;
}

.routineActivityDetails.title {
  font-weight: bold;
  font-size: 18px;
  position: relative;
  left: 0;
  max-width: 90%;
}
textarea {
  height: 50px;
  overflow-wrap: break-word;
  font-size: 15px;
}

.button {
}
#closeButton {
  border-radius: 8px;
  z-index: 100;
  width: 150px;
  height: 40px;
  min-height: 40px;
  border: 2px solid red;
  width: 15vw;
  margin-top: 5px;
  position: absolute;
  right: 0;
  bottom: 0;
  margin: 8px;
}

#closeButton:hover {
  background-color: red;
  color: white;
}

#addActivityButton {
  border-radius: 8px;

  z-index: 100;
  width: 150px;
  height: 40px;
  min-height: 40px;
  border: 2px solid blue;
  width: 15vw;
  margin-top: 5px;
  position: absolute;
  left: 0;
  bottom: 0;
  margin: 8px;
}

#addActivityButton:hover {
  background-color: blue;
  color: white;
}

.delActivityButton {
  float: right;
  position: relative;
  bottom: 22px;
  right: 5px;
}
footer {
  width: 100%;
  position: absolute;
  bottom: 0;
  background: green;
}

.createForm {
  display: flex;
  flex-direction: column;
}
.createActivity {
  left: 25%;
  top: 25%;
}
.createField {
  border: 1px solid gray;
  resize: none;
  margin: 10px;
  width: 90%;
  height: 60%;
  font-size: 18px;
}
.addActivity {
  background:white;
  bottom:0;
  z-index: 1000;
  position: sticky;
  min-height: 80px;
}
select {
  width: 50%;
}
#countInput {
  width: 20%;
}
#durInput {
  width: 20%;
}