.routines {
  background-image: linear-gradient(purple, white);
  background-size: cover;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  position: absolute;
  margin-top: 107px;
  margin-bottom: 20px;
  border-radius: 0;
  padding-bottom: 200px;
}

.routine {
  box-shadow: 0px 15px 10px -15px black;
  margin: 10px;
  margin-bottom: 20px;
  padding: 10px;
  width: 25vw;
  max-height: 125px;
  overflow-y: auto;
  background: white;
}
.routine:hover {
  box-shadow: 0px 15px 10px -15px red;
}

#activities {
  background-image: linear-gradient(silver, white);
  width: 100vw;
}
#activities .routine:hover {
  box-shadow: 0px 15px 10px -15px black;
}
#createActivity:hover {
  box-shadow: 0px 15px 10px -15px red;
}

#currentPage {
  color: green;
}
.pageNum,
.pageButton {
  background-color: white;
  border: 2px silver solid;
  background: white;
  font-size: 16px;
  font-family: sans-serif;
  font-weight: bold;
  padding: 4px;
  margin: 2px;
  min-width: 15px;
}
.pageNum:hover,
.pageButton:hover {
  text-decoration: underline green;
}

#pageButtonContainer {
  display: flex;
  min-height: 35px;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  z-index: 100;
  background: white;
  border-radius: 0;
  position: fixed;
  bottom: 0;
  width: 100vw;
}
#bottomSpacer {
  width: 100vw;
  min-height: 50px;
}
