.input {
  border: solid 1px;
  margin: 5px;
  border-radius: 0;
}

.loginPage {
  background-image: linear-gradient(green, white);
  top: 107px;
  position: absolute;
}

#loginPage {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 80%;
  border-radius: 0;
}

.boxes,
.loginTitle,
.register {
  margin: 10px;
  display: flex;
  flex-direction: column;
  align-items: space-between;
  position: relative;
  top: 20vh;
}

.loginTitle {
  -webkit-text-fill-color: rgb(19, 8, 8);
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: rgb(71, 241, 102);
}
